import React, {Component} from 'react';
import {Card,
         CardMedia,
         CardTitle,
} from 'material-ui/Card'
import TextField from "material-ui/TextField"
import Paper from "material-ui/Paper"
import RaisedButton from "material-ui/RaisedButton"
import call from "./api"
import AnswerDisplay from "../component/answerDispaly"
import update from 'immutability-helper'

class Home extends  Component{
    constructor(props) {
        super(props);
        this.state = {
            Q:null,
            isChange:0
        }

        this.styles = {
            searchButton: {
                marginLeft: "20%",
                marginRight: "20%",
            },
            card:{
                marginLeft: "20%",
                marginRight: "20%",
            },
            searchText:{
                paddingLeft: "15%"
            },
            div: {
                marginBottom: "70%"
            },
            paperQ: {
                fontSize: "x-large",
                marginTop: "30",
                paddingLeft:"15%",
                paddingRight: "15%",
                marginLeft: "10%",
                marginRight: "10%"
            },
            paperA: {
                fontSize: "larger",
                marginTop: "30",
                marginRight: "15%",
                marginLeft: "15%",
            }
        }
        this.handleChange = (e) => {
            this.data = null;
            this.setState(update(this.state,
                {
                    Q:{$set:e.currentTarget.value},
                    isChange:{$set:this.state.isChange+1}
                }))
            //console.log("change state: ",this.state)

        }

        this.handleClick = () => {
            this.data = null
            call("answers",
                this.state.Q,
                (data,err) =>{
                    if(data){
                        this.data = data;
                        console.log("data:  ",this.data)
                        this.setState(update(this.state,{isChange:{$set:this.state.isChange+1}}));
                    }
                }
            )
        }

        this.handleScoreChange = (answer,score) => {
            this.data[answer] = score;
            this.setState(update(this.state,{isChange:{$set:this.state.isChange}}));
            //this.setState(update(this.state,{$set:newstate}))
        }
    }
    render() {
        return(
            <div>
                <div style={this.styles.div}>
                    <Card  style={this.styles.card}>
                        <CardMedia overlay={<CardTitle title="BiBot" />} >
                            <img src="http://puu.sh/qsW1t/c30576d658.jpg"/>
                        </CardMedia>
                        <div>
                            <TextField defaultValue="search"
                                       onChange={this.handleChange}
                                       style={this.styles.searchText}
                            />
                            <RaisedButton label="search"
                                          primary={true}
                                          style={this.styles.searchButton}
                                          onClick={this.handleClick}
                            />
                        </div>
                    </Card>
                    <div>
                        {
                            (() => {
                                let component = [];
                                if (this.state.Q) {
                                    component.push(<Paper style={this.styles.paperQ}>{"Q: " + this.state.Q}</Paper>);
                                }
                                else
                                    return null;
                                if (this.data) {
                                    //console.log(this.data);
                                    let scores = [];
                                    let answers = [];
                                    let counter = 0;
                                    for (let a in this.data){
                                        scores.push(this.data[a])
                                        scores.push(counter)
                                        counter++;
                                        answers.push(a)
                                    }
                                    for(let i=0;i<scores.length;i+=2)
                                        for(let j=i+2;j<scores.length;j+=2){
                                            if(scores[i]<scores[j]){
                                                let temp = scores[j]
                                                scores[j] = scores[i]
                                                scores[i] = temp;
                                                temp = scores[i+1]
                                                scores[i+1] = scores[j+1]
                                                scores[j+1] = temp
                                            }
                                    }
                                    if (scores.length <= 10) {
                                        for (let i=0;i<scores.length;i+=2) {
                                            console.log(":   ");
                                            component.push(
                                                <AnswerDisplay style={this.styles.paper}
                                                               text={answers[scores[i+1]]}
                                                               score={scores[i]}
                                                               scoreChange={this.handleScoreChange}
                                                />
                                            )
                                        }
                                    }
                                    else {
                                        for (let i = 0; i < 10; i+=2) {
                                            component.push(
                                                <AnswerDisplay style={this.styles.paper}
                                                               text={answers[scores[i+1]]}
                                                               score={scores[i]}
                                                               scoreChange={this.handleScoreChange}
                                                />
                                            )
                                        }
                                    }
                                    return component;
                                }
                                else {
                                    component.push(
                                        <Paper style={this.styles.paperA}>{""}</Paper>
                                    )
                                    console.log(component)
                                    return component;
                                }
                            })()
                        }
                    </div>
                </div>
            </div>
        )
    }
}


export default Home;


