import React, {Component} from 'react';
import {Card,
    CardActions,
    CardText,
    CardHeader,
} from 'material-ui/Card'
import RaisedButton from "material-ui/RaisedButton"

class AnswerDisplay extends Component{
    constructor(props){
        super(props);

        this.handleClick = (e) => {
            if(e.currentTarget.id == "agree"){
                this.props.scoreChange(this.props.text,this.props.score+0.05)
            }
            else{
                this.props.scoreChange(this.props.text,this.props.score-0.05);
            }
        }
    }

    render(){
        return(
                <Card>
                    <CardHeader title={""+this.props.score}/>
                    <CardText>
                        {this.props.text}
                    </CardText>
                    <CardActions>
                        <RaisedButton label=""
                                      id="agree"
                                      onClick={this.handleClick}
                        />
                        <RaisedButton label=""
                                      id="disagree"
                                      onClick={this.handleClick}
                        />
                    </CardActions>
                </Card>
        )

    }



}

export default AnswerDisplay;


